.speisekartePage {
  display: flex;
  flex-direction: row;
  height: 70%;
  width: 100%;
  margin-top: 15%;
  justify-content: center;
  align-items: stretch;
  color: rgba(235, 235, 235, 0.8);
  font-family: "Yesteryear", cursive;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  font-size: 25px;
}

h1 {
  font-size: 35px;
  color: rgba(235, 235, 235, 0.8);
}

.box-1 {
  flex: 1;
}

.box-2 {
  display: flex;
  flex-direction: column;
  flex: 7;
  background-color: rgba(43, 43, 43, 0.7);
  padding-top: 50px;
  box-shadow: rgba(165, 165, 165, 0.3) 0px 13px 27px -5px,
    rgba(165, 165, 165, 0.3) 0px 8px 16px -8px;
}

.speisekartenLogo {
  flex: 1;
  background-image: url("../../images/sushi_logo_white_transparent.png");
  background-repeat: no-repeat;
  background-size: 250px 75px;
  background-position: center;
}

.anfahrtInfo {
  flex: 4;
}

.box-3 {
  flex: 1;
}

@media screen and (min-width: 500px) {
  .speisekartePage {
    height: 80%;
    margin-top: 5%;
    font-size: 28px;
  }
  h1 {
    font-size: 45px;
  }
  .box-2 {
    flex: 0.8;
    padding-top: 20px;
  }
  .anfahrtLogo {
    flex: 1;
    // background-image: url("../../images/sushi_logo_white_transparent.png");
    // background-repeat: no-repeat;
    // background-size: 280px 100px;
    // background-position: center;
  }
}
