.navbar {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  background-color: rgba(59, 59, 59, 0.8);
  color: rgba(220, 220, 220, 0.8);
  font-family: "Yesteryear", cursive;
  font-weight: 500;
  font-style: normal;
  font-size: 20px;
  padding-right: 10px;

  .logo-container {
    height: 100%;
    width: 100px;
    display: flex;
    margin-left: -40px;
    animation-name: moveInLeft;
    animation-duration: 1s;
  }

  .logo {
    width: 100%;
    height: auto;
  }

  .options {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .nav {
      padding: 0px 3px;
      margin-left: 5px;
      margin-right: 5px;
      color: rgba(220, 220, 220, 0.8);
      text-decoration: none;
      transition: 0.1s;
    }
  }

  .nav:visited {
    color: rgba(220, 220, 220, 0.8);
  }

  .nav:hover {
    -webkit-transform: scale(1.02);
    text-decoration: underline;
  }

  @keyframes moveInLeft {
    0% {
      opacity: 0;
      transform: translateX(-10px);
    }
    100% {
      opacity: 1;
      transform: translateX(0px);
    }
  }
}

@media screen and (min-width: 500px) {
  .navbar {
    font-size: 25px;
  }
  .nav {
    padding: 0px 5px;
    margin-left: 10px;
    margin-right: 10px;
  }
}
